    br
    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-left")
      p En conjunto, estos recursos conforman el patrimonio vivo del destino y son el eje que orienta la narrativa interpretativa, permitiendo que cada experiencia turística sea auténtica, significativa y sostenible.

    br
    .row.justify-content-center.align-items-center.mb-2(data-aos="fade-left")
      .col-md-1
      .col-md-11.mb-4.mb-md-0.my-2
        h3 Proceso comunicativo en la interpretación turística
        p Según Ham (2013), “la interpretación no es solo comunicación, sino comunicación con un propósito: provocar al visitante para que piense y se preocupe por el significado de lo que ve”.
        p Esta afirmación destaca que la interpretación turística es un proceso comunicativo intencionado, cuyo objetivo no es únicamente transmitir información, sino generar reflexión, emoción y compromiso en las personas visitantes frente al patrimonio natural y cultural.

    br
    .row.justify-content-center(data-aos="fade-left")
